










































import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
@Component({
     props: {
        control: {
            type: DiceController,
            required: true,
        },
    },
})
export default class DiceSuccess extends Vue {
    public buttonMusic: any = document.getElementById('diceButtonMusic');
    public timer: any = null;
    public mounted(): void {
        const musicSuccess: any = document.getElementById('diceSuccessMusic');
        const moneyMusic: any = document.getElementById('diceMoneyMusic');
        const musicFail: any = document.getElementById('diceFailMusic');
        this.timer = setTimeout(() => {
            this.submit();
        }, 2000);
        if ((this as any).control.isSoundOn  ) {
            if ((this as any).control.isWin()) {
                moneyMusic.play();
            } else {
                musicFail.play();
            }

        }
        const  timer: any = setTimeout(() => {
            if ( musicSuccess && (this as any).control.isSoundOn ) {
                musicSuccess.play();
            }
            window.clearTimeout(timer);
        }, 500);
    }
    private get getControl(): DiceController {
        return (this as any).control;
    }

    public get winCount(): number {
        return this.getControl.winCount;
    }

    public submit(): void {
       this.dismiss();
    }

    public dismiss(): void {
        if (this.timer) {
            window.clearTimeout(this.timer);
            this.timer = null;
        };
        this.getControl.clearResult();
    }
}
