












// @ts-ignore
import {Component, Vue} from 'vue-property-decorator';
import {DiceController} from '@/modules/contract/dice/diceController';
@Component({
    props: {
        control: {
            type: DiceController,
            required: true,
        },
    },
})
export default class DiceShakeH5 extends Vue {
    public mounted(): void {
        const box: any = document.getElementById('diceShake');
        const music: any = document.getElementById('diceShakeMusic');
        box.classList.add('begin');
        setTimeout(() => {
            box.classList.remove('begin');
            box.classList.add('shake');
            if ((this as any).control.isSoundOn) {
                music.play();
            }
        }, 500);
    }
}
